'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name base
* @memberof ns
* @readonly
* @type {Namespace}
* @see {@link module:@stdlib/wasm/base}
*/
import base from './../base';
setReadOnly( ns, 'base', base );

/**
* @name Memory
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/wasm/memory}
*/
import Memory from './../memory';
setReadOnly( ns, 'Memory', Memory );


// EXPORTS //

export default ns;
